import { gsap, SplitText, CustomEase } from 'vendors/gsap';

export default class CartierAnimation implements AnimationInterface {
  el?:HTMLElement;
  split?:SplitText;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {

      this.split = new SplitText(this.el, {
        // type: 'lines,words,chars',
        type: 'lines,words',
        // charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
      
      this.tl = gsap.timeline(
        { 
          paused: true,
          autoRemoveChildren: false,
          repeatRefresh: true,
        }
      )
        .fromTo(this.split.words, 
          {
            alpha: 0,
          },
          { 
            alpha: 1, 
            delay: 0.5,
            duration: 0.35, 
            stagger: 0.01,
            ease: 'linear'
          }, '0'
        )
        .fromTo(this.split.words, 
          {
            y: 20,
            rotateX: 90,
            scale: 0.7,
            z: 0
          },
          { 
            y: 0, 
            rotateX: 0,
            scale: 1,
            z: 0,
            delay: 0.5,
            duration: 0.8, 
            stagger: 0.01,
            force3D: true,
            ease: CustomEase.create('cartier', '.215,.61,.355,1'),
          }, '0'
        )
    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0);
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }

  update = () => {
    if (this.el) {
      this.split = new SplitText(this.el, {
        // type: 'lines,words,chars',
        type: 'lines,words',
        // charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
    }
  }
  
  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse();
    }
  }

  dispose() {
    this.tl?.kill();
    this.split?.revert();
    this.tl = undefined;
    this.split = undefined;
  }
}
